// 类型检测
export const isType = (value, type) => {
    const toString = {}.toString;
    return toString.call(value) === '[object ' + type + ']';
};
export const clone = (source) => {
    if (!source) {
        return source;
    }
    const target = {};
    for (let k in source) {
        target[k] = source[k];
    }
    return target;
};
/**
 * 存在时返回路径值，不存在时返回 undefined
 */
export const hasPath = (source, path) => {
    let current = source;
    for (let i = 0; i < path.length; i++) {
        if (current?.[path[i]]) {
            current = current[path[i]];
        }
        else {
            current = undefined;
            break;
        }
    }
    return current;
};
/**
 * 内部指定 params ，不考虑复杂情况
 */
export const setPath = (source, path, value) => {
    if (!source) {
        return source;
    }
    let o = source;
    path.forEach((key, idx) => {
        // 不是最后一个
        if (idx < path.length - 1) {
            o = o[key];
        }
        else {
            o[key] = value;
        }
    });
    return source;
};
